.dark-mode-section {
  width: 150px;
  display: grid;
  grid-template-columns: 60% 40%;
  margin: 0 auto;
}

.text-dark {
  margin: 3px;
  color: white;
}

.text-light {
  margin: 3px;
  color: black;
}

.toggle {
  display: inline-block;
  position: relative;
  margin: 3px;
  width: 52px;
  height: 25px;
}

.toggle input {
  display: none;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #fc7474;
  border-radius: 20px;
  transition: background-color 0.2s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 20px;
  width: 20px;
  left: 2.5px;
  top: 2.5px;
  background-color: white;
  border-radius: 50%;
  transition: transform 0.2s;
}

.toggle-text-left {
  font-size: 13px;
  margin: 2px 4px;
  text-align: left;
}

.toggle-text-right {
  font-size: 13px;
  margin: 2px 4px;
  text-align: right;
}

input:checked + .slider {
  background-color: #76fc74;
}

input:checked + .slider:before {
  transform: translateX(27px);
}
